<template>
  <div class="tool-container">
    <a href="" class="tTitle">
      <p></p>
      <p> ></p>
    </a>
    <div class="fucContainer">
      <a href="" class="fuc" v-for="tool in toolLists">
        <p class="fucImg"><img :src="tool.img" alt=""></p>
        <p class="fucName">{{ tool.name }}</p>
      </a>
    </div>
  </div>
</template>

<script>

  export default{
    data(){
      return{
        toolLists:[
          {img:require('../assets/myOwn/ticket.jpg'),name:''},
          {img:require('../assets/myOwn/cost.jpg'),name:''},
          {img:require('../assets/myOwn/leaguer.jpg'),name:''},
          {img:require('../assets/myOwn/mysec.jpg'),name:''},
          {img:require('../assets/myOwn/myQue.jpg'),name:'/'},
          {img:require('../assets/myOwn/myFri.jpg'),name:'/'},
          {img:require('../assets/myOwn/myComm.jpg'),name:'/'},
          {img:require('../assets/myOwn/myShar.jpg'),name:'/'},
        ]
      }
    }
  }


</script>



